.detail-product > p:nth-of-type(1) {
    border-bottom: 1px solid #ccc;
    margin: 0;
    padding: 20px 0px;
    text-transform: uppercase;
    font-size: 17px;
    letter-spacing: 1.5px;
}

.detail-product > p:nth-of-type(2), .detail-product > p:nth-of-type(3) {
    font-size: 12px;
    /* font-weight: 100; */
    display: flex;
    align-items: center;
    justify-content: center;
    font-family: 'Montserrat', sans-serif;
}

.detail-product > p svg {
    margin-right: 6px;
}
